import React,{useState,useEffect,useRef} from 'react'
import '../admission/FormPrint.css'
import Logo from '../../assets/img/logo.png'
import { useSelector } from 'react-redux'
import moment from 'moment'
import { fetchSemesterSlip } from '../../store/utils/ssoApi'
import { useReactToPrint } from 'react-to-print';

const PaperGroupSlip = () => {
    const { sso } = useSelector(state=>state)
    const { modal } = sso
    const [ courses, setCourses ] = useState([])
    const [ students, setStudents ] = useState({})
    const [ cdata, setCData ] = useState({})
    const [ mdata, setMdata ] = useState({})
    const [ title, setTitle ] = useState("")
    const [ level, setLevel ] = useState("")
    const [ user, setUser ] = useState({})
    
    const printRef = useRef();

    const handlePrint = useReactToPrint({
      content: () => printRef.current,
    });

    const genData = () => {
      const kt = {}
      let tt = "",lv = ""
      modal.content && modal.content.course_data && Object.entries(modal.content.course_data).map(([key,row]) => {
        console.log(row)
        for(const m of row){
          const mkey = `${m.course_code}${m.indexno}`
          tt = m.session_name
          lv = m.level
          kt[mkey] = row.find(dm => m.indexno === dm.indexno )
        }
      })
      setMdata({...kt})
      setTitle(tt)
      setLevel(lv)
      console.log(kt)
    }
    
    useEffect(() => {
        //modal.content && modal.content.data && modal.content.user && setUser({...modal.data.content.user});
        modal.content && modal.content.courses && setCourses([...modal.content.courses]);
        modal.content && modal.content.course_data && setCData({...modal.content.course_data});
        modal.content && modal.content.student_data && setStudents({...modal.content.student_data});
        console.log(modal.content)
        modal.content && genData()
    },[])

	
    return (
    <>
    <div className="row">
        <div className="Box small-12 columns" style={{width:'900px', margin:'0px auto',float:'none',overflow:'hidden'}}>
            <div className="small-12 columns u-pr-0 right">
                <button onClick={handlePrint} className="Button u-floatRight u-mb-2">&nbsp;&nbsp;Print&nbsp;&nbsp;</button>
            </div>
        </div>
     </div>
    <div className="print" ref={printRef}>
    <div className="cover">
       <content>
           {/* SLIP */}
        <div style={{marginTop:'40px'}}/>
         <section>
            <h1 className="h1title fore">AFRICAN UNIVERSITY COLLEGE OF COMMUNICATIONS, ACCRA</h1>
            <h2 className="h2title">{`LEVEL ${level}`}</h2>
            <h3 className="h3title">{`${title}`}</h3>
            <img src={Logo} style={{height:'90px', width:'90px',position:'relative',top:'-75px',left: '90px', margin:'0px auto -55px'}}/>
            <hr className="divider"/>
            <table className="stable">
                <tbody>
                    <tr className="thead">
                        <td className="codetitle" align="center">STUDENT</td>
                        <td className="tdtitle" align="center">COURSE</td>
                        { courses && courses.length > 0 ? courses.map((row) => <td className="tdtitle" align="center" style={{minWidth:'250px',fontSize:'11px'}}>{row}</td>): null }
                    </tr>
                    { students && Object.keys(students).length > 0 ?
                    Object.entries(students).map(([key,row]) =>
                    <tr class="tbody">
                        <td align="left">{row.name && row.name.toUpperCase()} <em><b>({row.indexno})</b></em></td>
                        <td align="center"><b>({row.count})</b></td>
                        { courses && courses.length > 0 ? courses.map((ct) => ct && mdata[`${ct}${row.indexno}`] ? <td className="tdtitle" align="center" style={{fontSize:'10px'}}><em>[C:<b>{mdata[`${ct}${row.indexno}`] && mdata[`${ct}${row.indexno}`].class_score || 0}</b>,E:<b>{mdata[`${ct}${row.indexno}`] && mdata[`${ct}${row.indexno}`].exam_score || 0}</b>]</em><br/><b style={{ color:'#b76117'}}>{mdata[`${ct}${row.indexno}`] && mdata[`${ct}${row.indexno}`].total_score || 0 }</b> - <b>{mdata[`${ct}${row.indexno}`] && mdata[`${ct}${row.indexno}`].grade}</b></td>: <td className="tdtitle" align="center" style={{color:'#b76117'}}>---</td>): null }
                    </tr>
                    ) : null }
                </tbody>
            </table>
            <hr className="divider"/>
            <h3 className="ftitle" style={{display:'none'}}>REGISTRATION IS ONLY VALID AFTER BIOMETRIC VERIFICATION</h3>
            <br/><br/>
         </section>
       </content>
    </div>
    </div>
    </> 
    )
}

export default PaperGroupSlip
